import gsap from "gsap";
import ScrollTrigger from "gsap/dist/ScrollTrigger";
import ScrollToPlugin from "gsap/dist/ScrollToPlugin";
gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
export default {
  layout: "defaultIndex",
  mounted: function () {
    var timer = null;
    var scrolled;
    var slide = 1;
    gsap
      .timeline()
      .to(".slide-content", { xPercent: 100, duration: 0 })
      .to(" .overlay", { xPercent: 100, zIndex: -1, duration: 0 })
      .to(".slide1 .overlay", { xPercent: 0, zIndex: -1, duration: 1 })
      .to(".slide1 .slide-content", { xPercent: 0, zIndex: 8, duration: 1 })
      .to(".slide1 .right-side img", { scale: 1.2, duration: 1.5 })
      .to(".slide1 .right-side img", { scale: 1, duration: 1.5 });
    window.addEventListener(
      "scroll",
      function () {
        if (this.window.innerWidth >= 992) {
        var winScrolla = window.pageYOffset;

        if (timer !== null) {
          clearTimeout(timer);
          window.scrollTo(0, winScrolla);
        }
        timer = setTimeout(function () {
          var winScroll = window.pageYOffset;
          var height =
            document.getElementsByClassName("slide1")[0].offsetHeight;
          if (slide == 1 && winScroll > 80) {
            gsap
              .timeline()
              .to(".slide1 .overlay", {
                xPercent: -100,
                zIndex: -1,
                duration: 0.7,
              })
              .to(
                ".slide1 .slide-content",
                {
                  xPercent: -100,
                  zIndex: 8,
                  duration: 0.7,
                },
                0
              );

            window.scrollTo(0, height);
            slide = 2;
            gsap
              .timeline()
              .to(".slide2 .overlay", {
                xPercent: 0,
                zIndex: -1,
                duration: 1.5,
              })
              .to(
                ".slide2 .slide-content",
                { xPercent: 0, zIndex: 8, duration: 1 },
                0.8
              )
              .to(".slide2 .right-side img", { scale: 1.1, duration: 1.5 })
              .to(".slide2 .right-side img", { scale: 1, duration: 1.5 });
          } else if (slide == 2 && winScroll > height + 80) {
            gsap
              .timeline()
              .to(".slide2 .overlay", {
                xPercent: -100,
                zIndex: -1,
                duration: 0.7,
              })
              .to(
                ".slide2 .slide-content",
                {
                  xPercent: -100,
                  zIndex: 8,
                  duration: 0.7,
                },
                0
              );
            window.scrollTo(0, (height*2));
            setTimeout(function(){slide = 3;}, 1000);
            
            gsap
              .timeline()
              .to(".slide3 .overlay", {
                xPercent: 0,
                zIndex: -1,
                duration: 1.5,
              })
              .to(
                ".slide3 .slide-content",
                { xPercent: 0, zIndex: 8, duration: 1 },
                0.8
              )
              .to(".slide3 .right-side img", { scale: 1.2, duration: 1.5 })
              .to(".slide3 .right-side img", { scale: 1, duration: 1.5 });
          } else if (slide == 2 && winScroll < height) {
            gsap
              .timeline()
              .to(".slide2 .overlay", {
                xPercent: -100,
                zIndex: -1,
                duration: 0.7,
              })
              .to(
                ".slide2 .slide-content",
                {
                  xPercent: -100,
                  zIndex: 8,
                  duration: 0.7,
                },
                0
              );
            window.scrollTo(0, 0);
            slide = 1;
            gsap
              .timeline()
              .to(".slide1 .overlay", {
                xPercent: 0,
                zIndex: -1,
                duration: 1.5,
              })
              .to(
                ".slide1 .slide-content",
                { xPercent: 0, zIndex: 8, duration: 1 },
                0.8
              )
              .to(".slide1 .right-side img", { scale: 1.2, duration: 1.5 })
              .to(".slide1 .right-side img", { scale: 1, duration: 1.5 });
          } else if (slide == 3 && winScroll < height * 2 - 80) {
            gsap
              .timeline()
              .to(".slide3 .overlay", {
                xPercent: -100,
                zIndex: -1,
                duration: 0.7,
              })
              .to(
                ".slide3 .slide-content",
                {
                  xPercent: -100,
                  zIndex: 8,
                  duration: 0.7,
                },
                0
              );
            window.scrollTo(0, height);
            slide = 2;
            gsap
              .timeline()
              .to(".slide2 .overlay", {
                xPercent: 0,
                zIndex: -1,
                duration: 1.5,
              })
              .to(
                ".slide2 .slide-content",
                { xPercent: 0, zIndex: 8, duration: 1 },
                0.8
              )
              .to(".slide2 .right-side img", { scale: 1.1, duration: 1.5 })
              .to(".slide2 .right-side img", { scale: 1, duration: 1.5 });
          }
          
        }, 150);
      }
      },
      false
    );
    
  },
  methods: {
    handleScroll() {
      var winScroll = window.pageYOffset;
      var height = document.getElementsByClassName("slide1")[0].offsetHeight;
      if (winScroll > 100) {
        window.scrollTo(0, height);
      }
    },

    scrollAnimation() {},
  },
};
